.header {
  background-color: #efefef;
  display: flex;
  justify-content: space-between;
  padding: 20px 50px;
  position: fixed;
  width: 100%;
  z-index: 100;
}
.header__name {
  display: flex;
  flex-basis: 0;
  flex-grow: 1;
  font-size: clamp(15px, 2.4vw, 24px);
  font-weight: 500;
  align-items: center;
}
.header__logo {
  animation-name: shrink;
  animation-timeline: scroll();
  animation-range: 0 350px;
  animation-fill-mode: both;
}
@keyframes shrink {
  from {
    font-size: clamp(54px, 9vw, 90px);
  }
  to {
    font-size: clamp(21px, 3.5vw, 35px);
  }
}

.mainmenu {
  align-items: center;
  display: flex;
  flex-basis: 0;
  flex-grow: 1;
  font-size: clamp(10px, 1.6vw, 16px);
  font-weight: 300;
  gap: 2rem;
  justify-content: flex-end;
  line-height: clamp(16px, 1.428vw, 20px);
  text-transform: uppercase;
}
.mainmenu ul {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}
@media only screen and (max-width: 990px) {
  .mainmenu {
    background-color: white;
    flex-direction: column;
    height: 0;
    overflow: hidden;
    position: fixed;
    right: 0;
    top: 0;
    transition: all 0.5s ease;
    width: 0;
    z-index: 101;
  }
  .mainmenu > * {
    padding: calc(fluid(110) + 3vw) fluid(20) fluid(20);
    opacity: 0;
    transition: all 0.1s ease;
  }
  .mainmenu.expanded {
    background-color: hsl(0, 50%, 50%);
    font-size: clamp(15px, 2.4vw, 24px);
    height: 100vh;
    padding: 5vw;
    pointer-events: all;
    width: 100vw;
  }
  .mainmenu.expanded > * {
    opacity: 1;
    transition: all 0.4s ease 0.4s;
  }
  .mainmenu.expanded ul {
    border-radius: 10px;
    flex-direction: column;
    gap: 1rem;
    padding: 2rem;
  }
  .mainmenu.expanded ul li {
    margin: 0.5rem 0;
  }
  .mainmenu.expanded ul li a:hover {
    color: #1a1a1a;
  }
}

.s-about__text {
  display: flex;
  flex-direction: column;
  font-size: clamp(12px, 2vw, 20px);
  gap: 1rem;
  margin-bottom: 2rem;
  max-width: 650px;
}

.s-image {
  height: 700px;
  max-height: 900px;
  view-timeline-name: --image;
  view-timeline-axis: block;
  animation-timeline: --image;
  animation-name: show;
  animation-fill-mode: both;
  animation-range: entry 25% cover 50%;
}
@keyframes show {
  from {
    opacity: 0.8;
    scale: 45%;
  }
  to {
    opacity: 1;
    scale: 100%;
  }
}

.s-services__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas: "image1 image2" "image1 image3" "image4 image5";
  gap: 1rem;
  max-height: 1000px;
}
.s-services__grid .image1 {
  grid-area: image1;
}
.s-services__grid .image2 {
  grid-area: image2;
}
.s-services__grid .image3 {
  grid-area: image3;
}
.s-services__grid .image4 {
  grid-area: image4;
}
.s-services__grid .image5 {
  grid-area: image5;
}
.s-services__figure {
  position: relative;
  border-radius: 10px;
  cursor: pointer;
  filter: grayscale(90%);
  overflow: hidden;
  transition: filter 0.3s ease-in-out;
  view-timeline-name: --item-timeline;
  animation: slide-fade-in both;
  animation-timeline: --item-timeline;
  animation-range: contain 0% contain 40%;
}
.s-services__figure:hover {
  filter: none;
}
.s-services__figure > img {
  width: 100%;
  object-fit: cover;
  height: auto;
}
@keyframes slide-fade-in {
  from {
    opacity: 0;
    transform: translateY(5vh);
  }
}
.s-services__overlay {
  background-color: rgba(0, 0, 0, 0.5);
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: 0;
}
.s-services__info-container {
  color: #f4bc43;
  opacity: 1;
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  padding: 1rem;
}
.s-services__info-container > h2 {
  font-size: clamp(54px, 9vw, 90px);
}
.s-services__info-container > h3 {
  font-size: clamp(18px, 3vw, 30px);
}
.s-services .image1 .s-services__info-container {
  top: 0;
  bottom: none;
}
.s-services .image2 .s-services__info-container {
  color: #89b5d4;
}
.s-services .image3 .s-services__info-container {
  color: #a0cf9f;
}
.s-services .image4 .s-services__info-container {
  color: #ea5e79;
}
.s-services .image5 .s-services__info-container {
  color: #7e77af;
}

.btn, .btn--header {
  background-color: white;
  border-radius: 1.5rem;
  border-style: solid;
  border-width: 0.06rem;
  color: black;
  cursor: pointer;
  display: inline-block;
  height: 3rem;
  line-height: 3rem;
  padding: 0 1em;
  position: relative;
  text-align: center;
  transition: background-color 0.3s ease, color 0.3s ease;
  width: fit-content;
}
.btn:hover, .btn--header:hover, .btn:active, .btn--header:active, .btn:focus, .btn--header:focus {
  background-color: black;
  color: white;
}

.btn--header {
  min-width: 9rem;
  font-size: 1.9rem;
  background-color: inherit;
}

.c-hamburger {
  align-items: center;
  cursor: pointer;
  display: none;
  flex-direction: column;
  height: 4rem;
  justify-content: center;
  margin-right: 1rem;
  position: relative;
  transition: all 0.2s ease-out;
  z-index: 102;
  /* Styling the hamburger lines */
}
@media only screen and (max-width: 990px) {
  .c-hamburger {
    display: flex;
  }
}
.c-hamburger__line {
  width: 22px;
  height: 4px;
  margin: 0 0 5px 0;
  background: crimson;
  transition: transform 0.5s ease, opacity 0.2s ease;
}
.c-hamburger__line:last-of-type {
  margin-bottom: 0;
}
.c-hamburger.expanded .c-hamburger__line {
  color: black;
  background: hsl(90, 90%, 90%);
  width: 29px;
}
.c-hamburger.expanded .c-hamburger__line:nth-child(1) {
  transform: rotate(45deg) translate(6.3px, 6.3px);
}
.c-hamburger.expanded .c-hamburger__line:nth-child(2) {
  opacity: 0;
}
.c-hamburger.expanded .c-hamburger__line:nth-child(3) {
  transform: rotate(-45deg) translate(6.3px, -6.3px);
}

.mango-widget {
  animation: boxshadow 2s ease-in-out infinite;
  bottom: 20px;
  position: fixed;
  right: 10px;
  z-index: 100;
}
.mango-widget img {
  max-height: 75px;
  height: auto;
  width: 100%;
}
@keyframes boxshadow {
  0% {
    filter: drop-shadow(0px 1px 2px black);
  }
  50% {
    filter: drop-shadow(0px 5px 5px black);
  }
  100% {
    filter: drop-shadow(0px 1px 2px black);
  }
}

main.template-home {
  min-height: 100dvh;
}
main.template-home section {
  padding: clamp(16px, 4.5vw, 50px);
}
main.template-home section.no-pad {
  padding: 0;
}
main.template-home > section > h2 {
  font-size: clamp(54px, 9vw, 90px);
}
main.template-home .s-main-title {
  font-size: clamp(78px, 13vw, 130px);
  padding: 150px 0px 0px 50px;
}
main.template-home .s-text > p {
  font-size: clamp(15px, 2.4vw, 24px);
  max-width: 900px;
}
main.template-home .swiper {
  max-width: 750px;
  height: 370px;
  display: grid;
  place-items: center;
}
main.template-home .swiper .swiper-slide {
  display: grid;
  place-items: center;
}
main.template-home .swiper .swiper-slide article {
  align-items: center;
  border-radius: 10px;
  box-shadow: 12px 12px 12px rgba(0, 0, 0, 0.1), -10px -10px 10px hsl(0, 12%, 97%);
  display: flex;
  flex-direction: column;
  gap: 1em;
  justify-content: center;
  height: 325px;
  width: 275px;
  padding: 24px;
}
main.template-home .swiper .swiper-slide article > div {
  overflow: hidden;
  max-height: 100px;
}
main.template-home .swiper .swiper-slide article img {
  filter: grayscale(1);
  height: 100%;
  width: 100%;
  object-fit: cover;
}
main.template-home .swiper .swiper-button-prev {
  align-items: center;
  color: black;
  display: flex;
  height: 150px;
  justify-content: center;
  width: 150px;
  top: 40%;
}
main.template-home .swiper .swiper-button-prev svg {
  transform: rotate(250deg);
  transition: color 0.3s ease;
}
main.template-home .swiper .swiper-button-prev:hover svg {
  color: #f4bc43;
}
main.template-home .swiper .swiper-button-prev:after {
  content: "";
}
main.template-home .swiper .swiper-button-next {
  align-items: center;
  color: black;
  display: flex;
  height: 150px;
  justify-content: center;
  width: 150px;
  top: 40%;
}
main.template-home .swiper .swiper-button-next svg {
  transform: rotate(70deg);
  transition: color 0.3s ease;
}
main.template-home .swiper .swiper-button-next:hover svg {
  color: #f4bc43;
}
main.template-home .swiper .swiper-button-next:after {
  content: "";
}