// --------------------------------------------------------------
// Section spacing
// --------------------------------------------------------------

$pad: clamp(16px, 4.5vw, 50px);

@mixin section-spacing {
  section {
    padding: $pad;
  }

  section.no-pad {
    padding: 0;
  }
}
